import React, {useState} from 'react';
import "mapbox-gl/dist/mapbox-gl.css";
import Map from "react-map-gl";

import res3 from '../data/res3-2023-08-05-03-39.json';
import res4 from '../data/res4-2023-08-05-03-39.json';
import res5 from '../data/res5-2023-08-05-03-39.json';
import res6 from '../data/res6-2023-08-05-03-39.json';
import res7 from '../data/res7-2023-08-05-03-39.json';
import res8 from '../data/res8-2023-08-05-03-39.json';
import res9 from '../data/res9-2023-08-05-03-39.json';
import res10 from '../data/res10-2023-08-05-03-39.json';

import Cell from './Cell';
import { cellToBoundary } from 'h3-js';
import {Source, Layer} from 'react-map-gl';




const styles = [
  {
    color: '#FEDD87',
    opacity: 0.2
  },
  {
    color: '#FED976',
    opacity: 0.4
  },
  {
    color: "#FC9653",
    opacity: 0.6,
  },
  {
    color: "#F77645",
    opacity: 0.7
  },
  {
    color: "#E14C48",
    opacity: 0.8
  }
];


function colorFromPrice(key, pricePerSqm) {
  const maxPrice = 10000;

  const perc = Math.round((pricePerSqm / maxPrice) * 100);
  let r = 0;
  let g = 0;
  let b = 0;
  if (perc > 100) {
    return '#FF0000';
  }
  if(perc < 50) {
    g = 255;
    r = Math.round(5.1 * perc);
  }
  else {
    r = 255;
    g = Math.round(510 - 5.10 * perc);
  }
  const h = r * 0x10000 + g * 0x100 + b * 0x1;
  return '#' + ('000000' + h.toString(16)).slice(-6);
}

function getDataForZoomLevel(zoomLevel) {
  if (zoomLevel > 15) {
    return res10;
  }

  if (zoomLevel > 13) {
    return res9;
  }

  if (zoomLevel > 12) {
    return res8;
  }

  if(zoomLevel > 10) {
    return res7;
  }

  if(zoomLevel > 8.5) {
    return res6;
  }
  if(zoomLevel > 7.5) {
    return res5;
  }
  if(zoomLevel > 6) {
    return res4;
  }
  return res3;
}

export default function ApartmentsHeatmap() {
  const [zoomLevel, setZoomLevel] = useState(8);
  const [mapZoomLevel, setMapZoomLevel] = useState(11);
  const data = getDataForZoomLevel(mapZoomLevel);
  const keys = Object.keys(data);

  function getFeatures() {
    return keys.map(key => {
      const {pricePerSqm, count} = data[key];
      return {
        type: 'Feature',
        properties: {
          color: colorFromPrice(key, pricePerSqm),
          opacity: 0.5,
          text: `${pricePerSqm.toFixed(0)} eur/m2\n${count} ads`,
          id: key,
        },
        geometry: {
          type: 'Polygon',
          coordinates: [cellToBoundary(key, true)]
        }
      };
    });
  }

  function onZoomEnd(newZoom) {
    setMapZoomLevel(newZoom.viewState.zoom);
    const zoom = Math.round(newZoom.viewState.zoom - 3)
    setZoomLevel(zoom);
  }

  const features = getFeatures();
  return <div className="Map">
    <div>
      {mapZoomLevel}
      <br />
      {zoomLevel}
    </div>
    <Map
      initialViewState={{
        latitude: 60.192059,
        longitude: 24.945831,
        zoom: 11,
        bearing: 0,
        pitch: 0,
      }}
      mapStyle="mapbox://styles/mapbox/streets-v9"
      mapboxAccessToken="pk.eyJ1IjoiYWxleGVpbW9pc3NlZXYiLCJhIjoiY2xreTJrZGRlMTd6NDNkcG16bTVnbmxjZiJ9.QJqrTtj51RKKpITnM6pjKA"
      style={{
        height: "100vh",
        width: "100vw",
      }}
      onZoomEnd={onZoomEnd}
    >
      <Source type="geojson"
                data={{
                  type: "FeatureCollection",
                  features: features
                }}>
        <Layer id="polyline-layer" type="fill" paint={{
          'fill-outline-color': 'white',
          "fill-color": ["get", "color"],
          "fill-opacity": ["get", "opacity"],
        }}/>

        <Layer type="symbol" layout={{
          "text-field": ['get', 'text'],
          "text-size": 11,
        }} />
  </Source>   
      
    </Map>
  </div>;
}